@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --highlight-color: #2d79b8;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    color: inherit;
    text-decoration: none;
}

body {
    background-color: #eee;
    color: #434344;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
}

/* Page Layout */

.App {
    max-width: 850px;
    margin: 0 auto;
    padding: 0 40px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
}

/* Header */

header{
    color: #9f9c9c;
    padding: 20px 0;
    margin-bottom: 40px;
    text-transform: uppercase;
    border-bottom: 2px solid #000000;
    display: flex;
    justify-content: space-between;
}

header nav ul {
    display: flex;
    align-items: baseline;
    list-style-type: none;
}

header nav li {
    margin-right: 15px;
}

header button {
    border: none;
    padding: 8px 25px;
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    border-radius: 1.25rem;
    background-color: #000000;
}

/* Main Section */

.wrapper {
    display: flex;
}

.main {
    flex: 4 1;
}

.main ul {
    padding-left: 20px;
    padding-bottom: 15px;
}

.main h2 {
    font-size: 32px;
    margin: 1rem 0;
    color: #2d79b8;
    color: var(--highlight-color);
}

.main h3 {
    padding-bottom: 10px;
}

.main p {
    font-size: 1rem;
    margin-bottom: .75rem;
}

.main img {
    width: 100%;
}

.main .ProjectPanel {
    padding-top: 15px;
}

/* Sidebar */

.sidebar {
    border-radius: 20px;
    padding: 20px;
    flex: 1 1;
    margin-right: 60px;
    border-top: 3px solid #2d79b8;
    border-top: 3px solid var(--highlight-color);;
    background-color: #f9f9f9;
}

.sidebar h3 {
    padding-bottom: 10px;
}

.sidebar ul {
    padding-bottom: 10px;
}

.sidebar ul {
  list-style-type: none;
  margin-top: 1rem;
  font-size: .75rem;
}

.sidebar ul > li {
    padding: .25rem 0;
}

.logo {
  font-size: 2rem;
  margin-right: 1.5rem;
  color: #50c6db;
}

/* Footer */

footer {
    color: #fff;
    text-align: center;
    padding: 20px 0;
    margin-top: 60px;
    padding: 20px 0;
    background-color: #9f9c9c;
    border-radius: 20px;
}

footer p {
    color: #434344;
    font-size: 12px;
    padding: 10px;
}

footer a {
    color: #2d79b8;
    color: var(--highlight-color);
}


@media (max-width: 600px) {
    .wrapper {
        flex-direction: column;
    }

    .main {
        margin-right: 0;
        margin-bottom: 60px;
    }
}
